import {
  pluginName
} from './constants';

const CKEDITOR = window.CKEDITOR;

function getFirstTextNode(element) {
  const children = element.getChildren();
  for (let i = 0; i < children.count(); i++) {
    const child = children.getItem(i);
    if (child.type === CKEDITOR.NODE_TEXT) {
      return child;
    }
  }
  return null;
}

CKEDITOR.plugins.add(pluginName, {
  requires: 'richcombo',
  init: function (editor) {
    const pluginConfig = CKEDITOR.tools.get_plugin_config(pluginName, editor);
    const colors = pluginConfig;

    editor.ui.addRichCombo('ApplyColor', {
      label: 'Apply Color',
      toolbar: 'styles',
      panel: {
        css: [CKEDITOR.skin.getPath('editor')].concat(editor.config.contentsCss),
        multiSelect: false
      },
      init: function () {
        this.add('default', 'Remove Color', 'Remove Color'); // Remove color default option
        console.log('colors', colors);
        for (let label in colors) {
          const colorValue = colors[label];
          this.add(colorValue, label, label);
        }
      },
      onRender: function () {
        this.setValue(''); // Prevents any item from being auto-selected
      },
      onClick: function (value) {
        editor.focus();
/*         if (value === 'default') {
          const selection = editor.getSelection();
          const ranges = selection.getRanges();

          editor.fire('lockSnapshot');

          console.log('üü° Starting color removal...');

          // Force span splitting by applying and removing a dummy style
          const dummyStyle = new CKEDITOR.style({ element: 'span', styles: { 'background-color': '#ffffff' } });
          editor.applyStyle(dummyStyle);
          editor.removeStyle(dummyStyle);

          const bookmarks = selection.createBookmarks();
          ranges.forEach((range, i) => {
            console.log(`üü¢ Range ${i + 1}:`, range);

            let found = 0;
            let unwrapped = 0;

            const tryProcessSpan = (span) => {
              if (
                span &&
                span.type === CKEDITOR.NODE_ELEMENT &&
                span.getName() === 'span' &&
                span.getStyle('color')
              ) {
                found++;
                console.log('üéØ Found span with color:', span.getOuterHtml());
                span.removeStyle('color');

                if (!span.hasAttributes()) {
                  const children = span.getChildren().toArray();
                  for (let child of children) {
                    span.insertBeforeMe(child.remove());
                  }
                  span.remove();
                  unwrapped++;
                  console.log('üßπ Unwrapped span.');
                }
              }
            };

            // 1. Try enclosed node
            const enclosed = range.getEnclosedNode();
            tryProcessSpan(enclosed);

            // 2. Fallback: walker inside the range
            const walker = new CKEDITOR.dom.walker(range);
            walker.evaluator = function (node) {
              return node.type === CKEDITOR.NODE_ELEMENT &&
                    node.getName() === 'span' &&
                    node.getStyle('color');
            };

            let node;
            while ((node = walker.next())) {
              tryProcessSpan(node);
            }

            // 3. NEW: Also check if startContainer or endContainer is a colored span
            tryProcessSpan(range.startContainer);
            tryProcessSpan(range.endContainer);

            console.log(`‚úÖ Range ${i + 1} done: found ${found}, unwrapped ${unwrapped}`);
          });

          editor.fire('unlockSnapshot');

          console.log('üéâ Finished removing color from selection.');
        } */
        if (value === 'default') {
          const selection = editor.getSelection();
          if (!selection) {
            console.warn('‚ö†Ô∏è No selection found.');
            return;
          }

          const bookmarks = selection.createBookmarks();
          const ranges = selection.getRanges();

          if (!ranges.length || ranges.every(r => r.collapsed)) {
            console.warn('‚ö†Ô∏è No text selected.');
            return;
          }

          editor.fire('lockSnapshot');
          console.log('üü° Starting precise color removal‚Ä¶');

          ranges.forEach((range, index) => {
            if (range.collapsed) {
              console.log(`‚è≠ Skipping collapsed range ${index + 1}`);
              return;
            }

            console.log(`üü¢ Processing range ${index + 1}`);
            range.enlarge(CKEDITOR.ENLARGE_INLINE);

            const walker = new CKEDITOR.dom.walker(range);
            walker.evaluator = function (node) {
              return (
                node.type === CKEDITOR.NODE_ELEMENT &&
                node.getName() === 'span' &&
                node.getStyle('color')
              );
            };

            let node;
            let spanCount = 0, cleanedCount = 0, unwrappedCount = 0;
            let foundAny = false;

            while ((node = walker.next())) {
              if (!node || !node.getStyle('color')) continue;
              foundAny = true;
              spanCount++;

              const nodeRange = editor.createRange();
              nodeRange.selectNodeContents(node);

              const startSpan = range.startContainer.getAscendant('span', true);
              const endSpan = range.endContainer.getAscendant('span', true);

              const needsSplit =
                startSpan &&
                endSpan &&
                startSpan.equals(node) &&
                endSpan.equals(node) &&
                !range.equals(nodeRange);

              if (needsSplit) {
                console.log('‚úÇÔ∏è Splitting span partially covered by selection.');

                const startRange = editor.createRange();
                startRange.setStart(range.startContainer, range.startOffset);
                startRange.setEndAfter(node);

                if (typeof startRange.split === 'function') {
                  startRange.split();
                } else {
                  console.warn('startRange.split is not a function');
                }

                const endRange = editor.createRange();
                endRange.setStart(range.endContainer, range.endOffset);
                endRange.setEndAfter(node);

                if (typeof endRange.split === 'function') {
                  endRange.split();
                } else {
                  console.warn('endRange.split is not a function');
                }

                let current = node.getNext();
                while (current) {
                  if (
                    current.type === CKEDITOR.NODE_ELEMENT &&
                    current.getName() === 'span' &&
                    current.getStyle('color') &&
                    current.getPrevious() &&
                    current.getPrevious().equals(node)
                  ) {
                    current.removeStyle('color');

                    if (!current.hasAttributes()) {
                      const children = current.getChildren().toArray();
                      for (let child of children) {
                        current.insertBeforeMe(child.remove());
                      }
                      current.remove();
                      unwrappedCount++;
                      console.log('üßπ Unwrapped empty span after color removal.');
                    } else {
                      console.log('üéØ Removed color, kept other styles:', current.getAttribute('style'));
                    }
                    cleanedCount++;
                    break;
                  }
                  current = current.getNext();
                }
              } else {
                node.removeStyle('color');
                cleanedCount++;

                if (!node.hasAttributes()) {
                  const children = node.getChildren().toArray();
                  for (let child of children) {
                    node.insertBeforeMe(child.remove());
                  }
                  node.remove();
                  unwrappedCount++;
                  console.log('üßπ Unwrapped empty span after color removal.');
                } else {
                  console.log('üéØ Removed color, kept other styles:', node.getAttribute('style'));
                }
              }
            }

            // üîÅ Fallback: try to remove color from nearest span if none found by walker
            if (!foundAny) {
              const colorSpan = range.startContainer.getAscendant('span', true);

              if (
                colorSpan &&
                colorSpan.type === CKEDITOR.NODE_ELEMENT &&
                colorSpan.getStyle('color')
              ) {
                console.log('‚ö†Ô∏è No color spans found in walker, but start is inside a color span ‚Äî fallback applying split.');

                const startContainer = range.startContainer;
                const endContainer = range.endContainer;

                // Ensure both containers are text nodes inside the span
                const isValidStart = startContainer.type === CKEDITOR.NODE_TEXT && startContainer.getParent()?.equals(colorSpan);
                const isValidEnd = endContainer.type === CKEDITOR.NODE_TEXT && endContainer.getParent()?.equals(colorSpan);

                if (isValidStart && isValidEnd) {
                  const startOffset = range.startOffset;
                  const endOffset = range.endOffset;

                  const originalText = startContainer.getText();
                  const before = originalText.slice(0, startOffset);
                  const middle = originalText.slice(startOffset, endOffset);
                  const after = originalText.slice(endOffset);

                  const parent = startContainer.getParent();

                  // Create new text nodes
                  const beforeNode = before ? new CKEDITOR.dom.text(before, editor.document) : null;
                  const middleNode = new CKEDITOR.dom.text(middle, editor.document);
                  const afterNode = after ? new CKEDITOR.dom.text(after, editor.document) : null;

                  // Replace original text node
                  startContainer.remove();

                  if (beforeNode) parent.append(beforeNode);
                  parent.append(middleNode);
                  if (afterNode) parent.append(afterNode);

                  // Wrap middleNode in a span that keeps other styles but removes color
                  const newSpan = new CKEDITOR.dom.element('span');
                  const oldStyle = colorSpan.getAttribute('style') || '';
                  const newStyle = oldStyle
                    .split(';')
                    .map(s => s.trim())
                    .filter(s => s && !s.startsWith('color'))
                    .join('; ');

                  if (newStyle) newSpan.setAttribute('style', newStyle);
                  parent.insertBefore(newSpan, middleNode);
                  newSpan.append(middleNode);

                  console.log('üéØ Removed color from selected part, preserved other styles.');
                  cleanedCount++;
                } else {
                  console.warn('‚ö†Ô∏è Could not split fallback text correctly ‚Äî selection may not be within a single span.');
                }
              }
            }

            console.log(`‚úÖ Finished range ${index + 1}: found=${spanCount}, cleaned=${cleanedCount}, unwrapped=${unwrappedCount}`);
          });

          selection.selectBookmarks(bookmarks);
          editor.fire('unlockSnapshot');
          console.log('üéâ Color removal complete.');
          return;
        }
        else {
          const style = new CKEDITOR.style({
            element: 'span',
            styles: { color: value }
          });
          editor.focus();
          editor.applyStyle(style);
        }
      }
    });
  }
});
